@import url("https://fonts.googleapis.com/css2?family=Poppins&family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

/* Navbar Styles */
.navbar {
  background-color: #f9f9f9; /* Blue background color */
  color: #000; /* Set text color to white */
  padding: 10px 20px;
}

.container-nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

h1 {
  margin: 0;
  font-size: 24px;
}
.navbar-links {
  padding: 10px;
}
.navbar-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.navbar-links ul li {
  margin: 0 15px;
  font-size: 18px;
}

.navbar-links a {
  text-decoration: none;
  color: #000;
  transition: color 0.3s;
}

/* Hover Effect */
.navbar-links a:hover {
  color: #4aa1f3; /* Black color when hovered */
}

/* Mobile Menu Styles */
.mobile-menu-toggle {
  display: none;
  cursor: pointer;
}

.bar {
  width: 25px;
  height: 4px;
  background-color: #000;
  margin: 4px 0;
}

/* Animations for Mobile Menu Toggle */
.mobile-menu-toggle.active .bar:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.mobile-menu-toggle.active .bar:nth-child(2) {
  opacity: 0;
}

.mobile-menu-toggle.active .bar:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -6px);
}

/* Media Query for Mobile Menu */
@media screen and (max-width: 768px) {
  .mobile-menu-toggle {
    display: block;
  }

  .navbar-links {
    display: none;
    position: absolute;
    top: 60px;
    right: 20px;
    flex-direction: column;
    background-color: #f9f9f9; /* Blue background for mobile menu */
    z-index: 1;
  }

  .navbar-links.active {
    display: flex;
  }
}

.textarea-container {
  margin: 20px;
  padding: 0px 20px;
}
.textarea-container h2 {
  color: #333; /* Dark gray text color */
  font-size: 24px; /* Larger text size for the heading */
  margin-left: 2px;
  margin-bottom: 10px;
}
.textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  color: #333;
  font-size: 16px;
  resize: none;
  transition: border-color 0.2s;
}

.textarea:hover,
.textarea:focus {
  border-color: #4aa1f3;
  outline: none;
}
/* Button CSS with Vibrant Blue Color */
.convert-button {
  background-color: #4aa1f3; /* Vibrant blue background color */
  color: #fff; /* White text color */
  font-size: 16px; /* Text size */
  padding: 10px 20px; /* Padding for comfortable clicking */
  border: none; /* Remove the default button border */
  border-radius: 5px; /* Rounded corners */
  cursor: pointer; /* Cursor style on hover */
  transition: background-color 0.2s; /* Smooth background color transition */
  margin: 5px;
}

.convert-button:hover {
  background-color: #357;
}
/* Container for Flexbox layout */
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Style for the text input container */
.text-input-container {
  flex: 1;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 20px;
  margin: 20px;
  text-align: center;
}

/* Style for the summary container */
.summary-container {
  flex: 1;
  border: 1px solid #4aa1f3; /* Theme color border */
  border-radius: 5px;
  padding: 20px 0px;
  margin: 20px;
  text-align: center;
  background-color: #f9f9f9; /* Lighter background color */
}

/* Heading style for Text Summary */
.summary-container h2 {
  color: #4aa1f3; /* Theme color text */
  font-size: 24px;
  margin-bottom: 10px;
}

/* Row layout for summary details */
.summary-details {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* Text Summary Details styles */
.summary-details div {
  color: #333;
  font-size: 16px;
  margin: 10px;
  flex: 1;
}
/* Style the "0" in blue */
.summary-details div #word-count,
.summary-details div #char-count {
  color: #4aa1f3; /* Theme color blue */
}
.summary-details div span {
  color: #4aa1f3; /* Theme color blue */
}
.cleanup-input {
  padding: 8px;
  border: 1px solid #4aa1f3; /* Border color that matches your theme */
  border-radius: 4px;
  font-size: 16px;
  margin-right: 10px; /* Adjust the margin as needed */
  background-color: #f9f9f9; /* Background color that matches your theme */
  color: #333; /* Text color that matches your theme */
}

.cleanup-input::placeholder {
  color: #999; /* Placeholder text color */
}
/* About.css */
.about-page {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}

.about-heading {
  color: #000; /* Text color matching your theme */
}

.feature-section {
  margin-top: 20px;
}

.feature-list {
  list-style-type: disc;
  margin-left: 20px;
}

.developer-section {
  margin-top: 20px;
}

/* Example: Text color */
.about-heading {
  color: #000; /* Black text color */
}
